.searchInputs {
 
    margin-top: 105px;
    display: flex;
  }
  
  .search input {
    background-color: white;
    border: 1;
    border-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    font-size: 18px;
    padding: 15px;
    height: 30px;
    width: 800px;
    margin-bottom: 10px;
  }
  
  .searchIcon {
    height: 60px;
    width: 50px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;

    background-color: white;
    display: grid;
    place-items: center;

  }
  
  input:focus {
    outline: none;
  }
  .searchIcon svg {
    font-size: 35px;
  }
  
  .dataResult {
    margin-top: 5px;
    width: 810px; 
  color: black;
    background-color: white;
    padding: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 5px;
    overflow: hidden;
    overflow-y: auto;
  }
  
  .dataResult::-webkit-scrollbar {
    display: none;
  }
  
  .dataResult .dataItem {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color: black;
  }
  
  .dataItem p {
    margin-left: 10px;
  }
  a {
    text-decoration: none;
  }
  
  a:hover {
    background-color: lightgrey;
  }
  
  #clearBtn {
    cursor: pointer;
  }
  .sss{
    display: flex;
    flex-direction: column;
    align-items: center;
  
  }
  .list{
  
    
display: flex;
    align-self: flex-start;
  }

